<template>
  <div>

    <h4>添加用户信息</h4>
    <form action="post">
      用户名：<input v-model="user.name" type="text"> <br>
      年龄：<input v-model="user.age" type="text"> <br>
      生日：<input v-model="user.bir" type="text"> <br>
      <input @click="saveUserInfo" type="button" value="提交">

    </form>
  </div>
</template>

<script>
  export default {
    name: "UserAdd",

    data() {
      return{

        user: {},
      }
    },
    methods: {
      saveUserInfo() {
        console.log(this.user);
        this.$http.post("http://localhost:8081/user/userAdd", this.user)
            .then((res) => {
              console.log(res);
              if (res.data.success) {
                this.$router.push("/user");//如果添加成功就将路由切换到user路由，显示添加的user信息，并将添加的那个页面隐藏掉！！
              }
            });
      }
    },

  }
</script>

