<template>
  <div>

    <h4>
      用户列表
    </h4>
    <table border="1">
      <tr>

        <td>编号</td>
        <td>姓名</td>
        <td>年龄</td>
        <td>生日</td>
        <td>操作</td>

      </tr>

      <tr v-for="user in users">

        <td>{{user.id}}</td>
        <td>{{user.name}}</td>
        <td>{{user.age}}</td>
        <td>{{user.bir}}</td>
        <td>
<!--          "javascript:;"阻止默认事件-->
          <a href="javascript:;" @click="delUser(user.id)">删除</a> |
          <a :href="'/user/userUpdate?id='+user.id">编辑</a>

        </td>

      </tr>

    </table>

    <a href="/user/userAdd">添加 </a>
    <router-view></router-view>


  </div>
</template>

<script>
  export default {
    name: "User",
    data() {
      return{
        users: [],
      }
    },

    methods:{
      findAll() {//查找所有用户
        this.$http.get('http://localhost:8081/user/findAll')
        // this.$http.get('http://rap2api.taobao.org/app/mock/273531/findAll')
            .then((res)=>{
              console.log(this);
              console.log(res.data)
              this.users = res.data.results
            });

      },

      delUser(id) {//删除用户
        console.log(id);
        this.$http.get("http://localhost:8081/user/userDel?id="+id).then((res) => {
          if (res.data.success) {
            alert(res.data.msg);
            this.findAll();
          }
        });

      }
    },

    components:{

    },

    created() {
      this.findAll();

    },

    watch: {//监听路由
      $route: {
        handler: function(val, oldVal){
          console.log(val);
          if (val.path == '/user') {
            this.findAll();

          }
        },
        // 深度观察监听
        deep: true
      }
    },

  };
</script>

