<template>
  <div>
    <h4>
      更新用户信息
    </h4>
    <form action="post">
      用户名：<input  v-model="user.name" type="text"> <br>
      年龄：<input  v-model="user.age" type="text"> <br>
      生日：<input  v-model="user.bir" type="text"> <br>
      <input @click="updateUserInfo" type="button" value="更新">

    </form>
  </div>
</template>

<script>
  export default {
    name: "UserUpdate",
    data() {
      return {
        user:{
          id: "",
        }
      };
    },
    methods: {

      findOne() {
        this.$http.get("http://localhost:8081/user/findOne?id=" + this.user.id).then((res) => {
          // console.log(res);
          console.log(res.data);
          this.user=res.data
        });
      },

      updateUserInfo() {
        this.$http.post("http://localhost:8081/user/updateUserInfo", this.user).then((res) => {
          if (res.data.success) {
            this.$router.push("/user");//动态转到/user 页面
          }

        });

      }
    },

    created() {
      console.log("修改组件中获取的id:" + this.$route.query.id);
      this.user.id = this.$route.query.id;
      this.findOne();
    }
  }
</script>

